.site-blocks-cover {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  background-position: center center;
  &.overlay {
    position: relative;
    &:before {
      position: absolute;
      content: "";
      left: 0;
      bottom: 0;
      right: 0;
      top: 0;
      background: rgba($black, 0.4);
    }
  }
  .player {
    position: absolute;
    bottom: -250px;
    width: 100%;
  }
  &,
  & > .container > .row {
    min-height: 600px;
    height: calc(100vh);
  }
  &.inner-page-cover {
    &,
    & > .container > .row {
      min-height: 400px;
      height: calc(20vh);
    }
  }

  h1 {
    font-size: 5rem;
    font-weight: 400;
    color: $white;
    font-weight: 900;
    @include media-breakpoint-down(md) {
      font-size: 2rem;
    }
  }
  p {
    color: rgba($white, 1);
    font-size: $font-size-base;
    line-height: 1.5;
    font-weight: 300;
    &.lead {
      font-size: 20px;
    }
  }

  .intro-text {
    font-size: 16px;
    line-height: 1.5;
  }
  .display-1 {
    @include media-breakpoint-down(md) {
      font-size: $font-size-base + 2rem;
    }
  }
}

.site-block-subscribe {
  .btn {
    padding-left: 20px;
    padding-right: 20px;
  }
  .form-control {
    font-weight: 300;
    border-color: $white !important;
    height: 80px;
    font-size: 22px;
    color: $white;
    padding-left: 30px;
    padding-right: 30px;
    &::-webkit-input-placeholder {
      /* Chrome/Opera/Safari */
      color: rgba($white, 0.5);
      font-weight: 200;
      font-style: italic;
    }
    &::-moz-placeholder {
      /* Firefox 19+ */
      color: rgba($white, 0.5);
      font-weight: 200;
      font-style: italic;
    }
    &:-ms-input-placeholder {
      /* IE 10+ */
      color: rgba($white, 0.5);
      font-weight: 200;
      font-style: italic;
    }
    &:-moz-placeholder {
      /* Firefox 18- */
      color: rgba($white, 0.5);
      font-weight: 200;
      font-style: italic;
    }
  }
}

.bg-light {
  background: #ccc;
}

.feature-blocks-1 {
  position: relative;
  margin-top: -70px;
  z-index: 2;
  color: darken($white, 40%);

  &.no-margin-top {
    margin-top: 0px;
  }

  .feature-block-1 {
    position: relative;
    top: 0;
    transition: 0.3s all ease;
    overflow: hidden;
    z-index: 1;
    &.bg {
      position: relative;
      background-size: cover;
      transform: scale(1);
      transition: 0.3s all ease-in-out;
    }
    .text {
      z-index: 10;
      position: relative;
      opacity: 0;
      visibility: hidden;
      transition: 0.3s all ease-in-out;
    }
    &:before {
      content: "";
      position: absolute;
      top: 0;
      z-index: 1;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba($black, 0);
      transition: 0.3s all ease-in-out;
    }
    p {
      color: lighten($black, 80%);
    }
    &:hover,
    &:focus,
    &:active {
      .text {
        opacity: 1;
        visibility: visible;
      }
      &.bg {
        position: relative;
        background-size: cover;
        transform: scale(1.1);
        z-index: 2;
      }
      top: -10px;
      box-shadow: 0 10px 40px -5px rgba(0, 0, 0, 0.4);
      &:before {
        background: rgba($black, 0.9);
      }
    }
  }
}

.site-blocks-vs {
  .bg-image {
    padding-top: 3em;
    padding-bottom: 3em;
    @include media-breakpoint-up(lg) {
      padding-top: 5em;
      padding-bottom: 5em;
    }
  }
  .image {
    width: 80px;
    flex: 0 0 80px;
    @include media-breakpoint-down(md) {
      margin-left: auto !important;
      margin-right: auto !important;
    }
    img {
      border-radius: 50%;
    }
    &.image-small {
      width: 50px;
      flex: 0 0 50px;
    }
  }
  .country {
    color: rgba($white, 0.7);
  }
}

.match-entry {
  position: relative;
  transition: 0.2s all ease-in-out;
  border-bottom: 1px solid rgba($black, 0.05);
  .small {
    color: #ccc;
    font-weight: normal;
    letter-spacing: 0.15em;
  }
  &:hover {
    transform: scale(1.05);
    z-index: 90;
    box-shadow: 0 5px 40px -5px rgba(0, 0, 0, 0.1);
  }
}

.post-entry {
  position: relative;
  overflow: hidden;
  border: 1px solid lighten($black, 90%);
  &,
  .text {
    border-radius: 4px;
    border-top: none !important;
  }
  .date {
    color: #ccc;
    font-weight: normal;
    letter-spacing: 0.1em;
  }

  .image {
    overflow: hidden;
    position: relative;
    img {
      transition: 0.3s all ease;
      transform: scale(1);
    }
  }
  &:hover,
  &:active,
  &:focus {
    .image {
      img {
        transform: scale(1.2);
      }
    }
  }
}

.site-block-tab {
  .nav-item {
    text-transform: uppercase;
    font-size: $font-size-base - 0.2rem;
    margin: 0 20px;
    &:first-child {
      margin-left: 0 !important;
    }
    > a {
      padding-left: 0 !important;
      background: none !important;
      padding-right: 0 !important;
      border-bottom: 2px solid transparent;
      border-radius: 0 !important;
      font-weight: bold;
      color: lighten($black, 80%);

      &:hover,
      &.active {
        border-bottom: 2px solid $primary;
        color: $black;
      }
    }
  }
}

.block-13,
.slide-one-item {
  position: relative;
  z-index: 1;
  .owl-nav {
    position: relative;
    position: absolute;
    bottom: -90px;
    left: 50%;
    transform: translateX(-50%);
    .owl-prev,
    .owl-next {
      position: relative;
      display: inline-block;
      padding: 20px;
      font-size: 30px;
      color: $black;
      &.disabled {
        opacity: 0.2;
      }
    }
  }
}

.slide-one-item {
  &.home-slider {
    .owl-nav {
      // transition: .3s all ease-in-out;
      // opacity: 0;
      // visibility: hidden;
      position: absolute !important;
      top: 50% !important;
      bottom: auto !important;
      width: 100%;
      @include media-breakpoint-down(md) {
        display: none;
      }
    }
    .owl-prev {
      left: 10px !important;
    }
    .owl-next {
      right: 10px !important;
    }
    .owl-prev,
    .owl-next {
      color: $white;
      position: absolute !important;
      top: 50%;
      padding: 0px;
      height: 50px;
      width: 50px;
      border-radius: 50%;
      transform: translateY(-50%);
      background: rgba($black, 0.2);
      transition: 0.3s all ease-in-out;

      line-height: 0;
      text-align: center;
      font-size: 25px;
      @include media-breakpoint-up(md) {
        font-size: 25px;
      }
      > span {
        position: absolute;
        line-height: 0;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }

      &:hover,
      &:focus {
        background: rgba($black, 1);
      }
    }

    &:hover,
    &:focus,
    &:active {
      .owl-nav {
        opacity: 10;
        visibility: visible;
      }
    }
  }
  .owl-dots {
    text-align: center;
    .owl-dot {
      display: inline-block;
      margin: 4px;
      span {
        display: inline-block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: $gray-300;
      }
      &.active {
        span {
          background: $primary;
        }
      }
    }
  }
}

.block-12 {
  position: relative;
  figure {
    position: relative;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: -moz-linear-gradient(
        top,
        transparent 0%,
        transparent 18%,
        rgba(0, 0, 0, 0.8) 99%,
        rgba(0, 0, 0, 0.8) 100%
      );
      background: -webkit-linear-gradient(
        top,
        transparent 0%,
        transparent 18%,
        rgba(0, 0, 0, 0.8) 99%,
        rgba(0, 0, 0, 0.8) 100%
      );
      background: linear-gradient(
        to bottom,
        transparent 0%,
        transparent 18%,
        rgba(0, 0, 0, 0.8) 99%,
        rgba(0, 0, 0, 0.8) 100%
      );
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#cc000000',GradientType=0 );
    }
  }
  .text-overlay {
    position: absolute;
    bottom: 20px;
    left: 20px;
    right: 20px;
    z-index: 10;
    h2 {
      color: $white;
    }
  }
  .text {
    position: relative;
    top: -100px;
    .meta {
      text-transform: uppercase;
      padding-left: 40px;
      color: $white;
      display: block;
      margin-bottom: 20px;
    }
    .text-inner {
      box-shadow: 0 0 20px -5px rgba(0, 0, 0, 0.3);
      background: $white;
      padding: 10px;
      margin-right: 30px;
      position: relative;
      @include media-breakpoint-up(sm) {
        padding: 20px;
      }
      @include media-breakpoint-up(md) {
        padding: 30px 40px;
      }

      &:before {
        position: absolute;
        content: "";
        width: 80px;
        height: 4px;
        margin-top: -4px;
        background: $primary;
        top: 0;
      }
      .heading {
        font-size: 20px;
        margin: 0;
        padding: 0;
      }
    }
  }
}

.block-16 {
  figure {
    position: relative;
    .play-button {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 20px;
      width: 70px;
      height: 70px;
      background: $white;
      display: block;
      border-radius: 50%;
      opacity: 1;
      color: $primary !important;
      &:hover {
        opacity: 1;
      }
      > span {
        position: absolute;
        left: 55%;
        top: 50%;
        transform: translate(-60%, -50%);
      }
    }
  }
}

.block-25 {
  ul {
    &,
    li {
      padding: 0;
      margin: 0;
    }
    li {
      a {
        .image {
          flex: 0 0 90px;
          img {
            border-radius: 4px;
            max-width: 100%;
            opacity: 1;
            transition: 0.3s all ease-in-out;
          }
        }
        .text {
          .heading {
            font-size: 18px;
            line-height: 1.5;
            margin: 0;
            padding: 0;
            transition: 0.3s all ease;
            color: lighten($black, 60%);
          }
        }
        .meta {
          color: $primary;
        }
        &:hover {
          img {
            opacity: 0.5;
          }
          .text {
            .heading {
              color: $white;
            }
          }
        }
      }
    }
  }
}

