      font-size: 22px;
      &,
      a {
        color: $white;
      }
    }
    span {
      color: $white;
    }
    .address {
      color: rgba($white, 0.8);
    }
  }

  .bookmark {
    position: absolute;
    right: 20px;
    bottom: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: inline-block;
    background: rgba($white, 0.3);
    transition: 0.3s all ease;
    span {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    &:hover {
      background: $danger;
      span {
        color: $white;
      }
    }
  }
}

