.box-with-humber {
  position: relative;
  .number-behind {
    font-size: 6rem;
    position: absolute;
    z-index: -1;
    top: -3rem;
    left: -3rem;
    font-weight: 900;
    color: lighten($gray-200, 3%);
  }
  h2 {
    font-size: 20px;
  }
}

.media-1 {
  position: relative;
  display: block;
  overflow: hidden;
  margin-bottom: 30px;
  &:after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    content: "";
    transition: 0.3s all ease;
    opacity: 0;
    visibility: hidden;
  }
  img {
    object-fit: cover;
    transition: 0.3s all ease;
  }
  .media-1-content {
    z-index: 4;
    top: 50%;
    position: absolute;
    width: 100%;
    text-align: center;
    transform: translateY(-50%);
    h2 {
      position: relative;
      color: $white;
      font-size: 1.5rem;
      transition: 0.4s all ease-in-out;
      opacity: 0;
      visibility: hidden;
      bottom: -10px;
      margin-bottom: 0px;
    }
    .category {
      position: relative;
      transition: 0.3s all ease-in-out;
      opacity: 0;
      bottom: -10px;
      visibility: hidden;
      color: darken($white, 40%);
      font-size: 13px;
      text-transform: uppercase;
      letter-spacing: 0.2em;
    }
  }
  &:hover {
    &:after {
      opacity: 1;
      visibility: visible;
    }
    h2 {
      bottom: 0px;
      opacity: 1;
      visibility: visible;
    }
    .category {
      opacity: 1;
      visibility: visible;
      bottom: 0px;
      transition-delay: 0.09s;
      // animation-direction: alternate;
    }
    img {
      transform: scale(1.05);
    }
  }
}

