.product-item {
  text-align: center;
  figure {
    overflow: hidden;
    position: relative;
    margin: 30px auto;
    width: 350px;
    height: 350px;
    &:before {
      z-index: 2;
      transition: 0.4s all ease;
      transform: scale(1.05);
      position: absolute;
      content: "";
      border: 4px solid $primary;
      left: 20px;
      right: 20px;
      top: 20px;
      bottom: 20px;
      opacity: 0;
      visibility: hidden;
    }
    img {
      transition: 0.3s all ease;
      transform: scale(1);
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  h3 {
    font-size: 18px;
    a {
      color: $black;
      &:hover {
        color: $primary;
      }
    }
  }
  p {
    line-height: 1.4;
    font-size: 13px;
  }

  .meta-icons {
    margin-left: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
    font-size: 13px;
    a {
      font-size: 16px;
      position: relative;
      top: 2px;
    }
  }
  &:hover {
    figure {
      &:before {
        opacity: 1;
        visibility: visible;
        transform: scale(1);
      }
      img {
        transform: scale(1.2);
      }
    }
  }
}

